import React ,{useState} from 'react'
import "./TodoList.css";
export default function TodoList() {
    const [todo, setTodo] = useState([
        {
            title:"Mua bim bim",
            done: false
        },
        {
            title:"Đi ngủ",
            done: false
        },
        {
            title:"Ăn cơm",
            done: false
        }
    ])
    return (
        <div className="TodoList">
            <h3>DANH SÁCH CẦN LÀM</h3>
            <input type="text" placeholder="Thêm việc cần làm"></input>
            <button>Thêm</button>
            <div className="todo-items">
                {todo.map((todo,index)=>(
                    <p key={index}>{todo.title}</p>
                ))}
            </div>
        </div>
    )
}
